.services {
  &__wrapper {
    background-image: url(https://res.cloudinary.com/de2ofvts5/image/upload/f_auto/v1600534234/altr/kv-page-lain_cplgej.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }
  .heading {
    max-width: 225px;
    @media only screen and (min-width: 768px) {
      align-self: flex-end;
      position: fixed;
      bottom: 0;
    }
    h1 {
      font-family: "GTAmerica-ExtendedBlack";
      font-size: 3.25rem;
      letter-spacing: 0.6rem;
      text-transform: uppercase;
      @media only screen and (min-width: 768px) {
        overflow-wrap: break-word;
        word-wrap: break-word;
        -webkit-hyphens: auto;
        -ms-hyphens: auto;
        -moz-hyphens: auto;
        hyphens: auto;
      }
    }
  }
  ul {
    @media only screen and (min-width: 768px) {
      padding-left: 325px;
      margin-top: 3rem;
    }
    @media only screen and (max-width: 768px) {
      padding-bottom: 5rem;
    }
    li {
      max-width: 638px;
      font-family: "GTAmerica-ExtendedMedium";
      font-size: 1rem;
      margin-bottom: 2.5rem;
      &.active {
        position: relative;
        font-family: "GTAmerica-ExtendedBlack";
        &::before {
          content: url("../../images/arrow.svg");
          position: absolute;
          right: 100%;
          padding-right: 2rem;
        }
      }
      a {
        color: #fff;
        cursor: pointer;
      }
      .content-subitem {
        display: none;
        border-top: 2px solid #fff;
        padding-top: 1.5rem;
        p {
          font-family: "GTAmerica-Extended";
        }
      }
    }
  }
  .video-item {
    margin-bottom: 1rem;
    img {
      border-radius: 1rem;
      height: 100%;
      object-fit: cover;
    }
    .play-button {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        cursor: pointer;
      }
    }
  }
  .video-player {
    display: none;
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    height: 0;
    margin-bottom: 2rem;
  }
  .video-player iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .socmed {
    font-family: "GTAmerica-ExtendedBold";
    font-size: 1rem;
    i {
      margin-right: 1rem;
    }
    @media only screen and (min-width: 768px) {
      padding-left: 35vw;
      margin-top: 3rem;
      font-size: 1.9rem;
    }
  }
  .address {
    position: fixed;
    bottom: 0;
    right: 0;
    text-align: right;
    padding-right: 5rem;
    font-size: 1.1rem;
    font-family: "GTAmerica-ExtendedMedium";
    &--mobile {
      position: relative !important;
      text-align: left;
      margin-bottom: 2rem;
    }
  }
}
